import React from 'react';
import ReactDOM from 'react-dom';
function addStart(){
    let $SMCCPC=document.getElementsByClassName("SMCCPC")[0];
    let $top_pacman=$(".top-pacman");
    let $bottom_pacman=$(".bottom-pacman");
    let $pacman=$(".pacman");
    let $bracket=$(".bracket");
    let $logo=$(".logo");
    $SMCCPC.addEventListener("click",startAnimation, false);
    function startAnimation(){
        $bracket.css({"opacity":0,"animation-play-state":"paused"});
        $top_pacman.removeClass("SMC");
        $bottom_pacman.removeClass("CPC");
        setTimeout(() =>{
            $top_pacman.addClass("pacman-top-running");
            $bottom_pacman.addClass("pacman-bottom-running");

            $logo.animate({ "left": "+=100px" }, 4000);
        },500);
    }
}

