    function getRandomIntInclusive(min, max) {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min + 1)) + min;
    }
    function randomSVG(){
        return SVG_all[getRandomIntInclusive(0,SVG_all.length)]
    }




    $(document).on("click",()=>{
        let props = {path:SVG_all[randomSVG()],scale:getRandomIntInclusive(100,400).toString()+"px",id:number_SVG.toString()};
        number_SVG+=1;
        ReactDOM.render(
            addSVG(props)
        );

        $("#"+props.id).velocity({translateX: 400,
            translateY: 250});

    });
